.fade-in-linear-enter-active,
.fade-in-linear-leave-active {
  transition: opacity 200ms linear; }

.fade-in-linear-enter,
.fade-in-linear-leave,
.fade-in-linear-leave-active {
  opacity: 0; }

/** tu-fade-in-linear */
.tu-fade-in-easeout-enter-active,
.tu-fade-in-easeout-leave-active {
  transition: opacity 0.3s ease-out; }

.tu-fade-in-easeout-enter,
.tu-fade-in-easeout-leave,
.tu-fade-in-easeout-leave-active {
  opacity: 0; }

/** tu-fade-in */
.tu-fade-in-enter-active,
.tu-fade-in-leave-active {
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-fade-in-enter,
.tu-fade-in-leave-active {
  opacity: 0; }

/** tu-popper-in-center */
.tu-popper-in-center-enter-active,
.tu-popper-in-center-leave-active {
  transition: all 0.2s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-popper-in-center-enter,
.tu-popper-in-center-leave-active {
  scale: 0;
  transform: scaleX(0); }

/** tu-zoom-in-center */
.tu-zoom-in-center-enter-active,
.tu-zoom-in-center-leave-active {
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-zoom-in-center-enter,
.tu-zoom-in-center-leave-active {
  opacity: 0;
  transform: scaleX(0); }

/** tu-zoom-in-top */
.tu-zoom-in-top-enter-active,
.tu-zoom-in-top-leave-active {
  opacity: 1;
  transform: scaleY(1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: center top; }

.tu-zoom-in-top-enter,
.tu-zoom-in-top-leave-active {
  opacity: 0;
  transform: scaleY(0); }

.tu-zoom-in-bottom-enter-active,
.tu-zoom-in-bottom-leave-active {
  opacity: 1;
  transform: scaleY(1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: center bottom; }

.tu-zoom-in-bottom-enter,
.tu-zoom-in-bottom-leave-active {
  opacity: 0;
  transform: scaleY(0); }

/** tu-zoom-in-left */
.tu-zoom-in-left-enter-active,
.tu-zoom-in-left-leave-active {
  opacity: 1;
  transform: scale(1, 1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: top left; }

.tu-zoom-in-left-enter,
.tu-zoom-in-left-leave-active {
  opacity: 0;
  transform: scale(0.45, 0.45); }

.collapse-transition {
  transition: 0.3s height ease-in-out, 0.3s padding-top ease-in-out, 0.3s padding-bottom ease-in-out; }

.horizontal-collapse-transition {
  transition: 0.3s width ease-in-out, 0.3s padding-left ease-in-out, 0.3s padding-right ease-in-out; }

.tu-list-enter-active,
.tu-list-leave-active {
  transition: all 0.48s; }

.tu-list-enter,
.tu-list-leave-active {
  opacity: 0;
  transform: translateX(-40px); }

.tu-opacity-transition {
  transition: opacity 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

html,
body {
  height: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

body {
  -webkit-font-smoothing: antialiased;
  font-size: 14px;
  color: #9baacf;
  -webkit-font-smoothing: antialiased; }

/* BEM [block]__[element]--[modifier]
 -------------------------- */
.tu-cascader-panel {
  display: flex;
  border-radius: 4px;
  font-size: 14px; }
  .tu-cascader-panel.is-bordered {
    box-shadow: inset 2px 2px 5px #c8d0e7, inset -2px -2px 5px #ffffff; }

.tu-cascader-menu {
  width: 100%;
  min-width: 180px;
  box-sizing: border-box;
  color: #9baacf;
  border-right: solid 1px rgba(190, 200, 228, 0.38); }
  .tu-cascader-menu:last-child {
    border-right: none; }
    .tu-cascader-menu:last-child .tu-cascader-node {
      padding-right: 20px; }
  .tu-cascader-menu__wrap {
    height: 212px;
    max-height: 212px;
    min-height: 64px; }
  .tu-cascader-menu__list {
    position: relative;
    min-height: 100%;
    width: 100%;
    margin: 0;
    padding: 6px 0;
    list-style: none;
    box-sizing: border-box; }
  .tu-cascader-menu__hover-zone {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none; }
  .tu-cascader-menu__empty-text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    color: #bec8e4; }

.tu-cascader-node {
  position: relative;
  display: flex;
  align-items: center;
  padding: 0 30px 0 20px;
  height: 36px;
  line-height: 36px;
  outline: none;
  transition: background-color 0.3s; }
  .tu-cascader-node.is-selectable.in-active-path {
    color: #9baacf; }
  .tu-cascader-node.in-active-path, .tu-cascader-node.is-selectable.in-checked-path, .tu-cascader-node.is-active {
    color: #6d5dfc; }
  .tu-cascader-node:not(.is-disabled) {
    cursor: pointer; }
    .tu-cascader-node:not(.is-disabled):hover, .tu-cascader-node:not(.is-disabled):focus {
      background-color: rgba(200, 208, 231, 0.38); }
  .tu-cascader-node.is-disabled {
    color: #bec8e4;
    cursor: not-allowed; }
  .tu-cascader-node__prefix {
    position: absolute;
    left: 10px; }
  .tu-cascader-node__postfix {
    position: absolute;
    right: 10px; }
  .tu-cascader-node__label {
    flex: 1;
    padding: 0 10px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis; }
  .tu-cascader-node > .tu-radio {
    margin-right: 0; }
    .tu-cascader-node > .tu-radio .tu-radio__label {
      padding-left: 0; }
  .tu-cascader-node > .tu-checkbox {
    margin-right: 0; }
    .tu-cascader-node > .tu-checkbox .tu-checkbox__label {
      padding-left: 0; }
